    padding: 0;
    margin: 0;
    font-family: 'IBM Plex Sans';
}

ul {
    list-style: none;
}

body {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-size: cover;
    background-repeat: no-repeat;
    color: rgb(232, 221, 221);
    padding: 5rem;

    

    .top {
        display: flex;
        justify-content: space-between;

        .top-left {
            display: flex;
            flex-direction: column;
            gap: .5rem;

            .country {
                font-weight: 500;
            }
            
            .state {
                font-size: 2.5rem;
            }

            .line-break {
                border: 1px solid white;
            }

            .temperature {
                font-size: 3rem;
                font-weight: bold;
            }

            .cloud-info {
                font-size: 1.5rem;
            }

            .weather-img {
                width: 100px;
                height: 100px;
            }
        }
    }

    .search-container {
        display: flex;
        flex-direction: column;
        gap: 1rem;
        align-items: center;
        position: relative;

        form {
            border-bottom: 1px solid white;
            display: flex;
            align-items: center;
            

            #search {
                background: transparent;
                border: none;
                font-size: 1.5rem;
                color: rgb(232, 221, 221);
                padding: .3rem .1rem;
                width: 400px;
            }

            #search:focus {
                outline: none;
            }

            #search::placeholder {
                color: rgb(232, 221, 221);;
            }

            .search-img {
                width: 25px;
                height: 25px;
            }
            
            .submit {
                background: transparent;
                border: none;
                cursor: pointer;
            }

            .submit:hover {
                transform: scale(1.15);
                transition: .2s;
            }

            .submit:active {
                transform: scale(0.95);
                transition: .2;
            }
        }

        .set-default {
            background: transparent;
            padding: .5rem 1rem;
            color: rgb(232, 221, 221);
            border: 1px solid rgb(232, 221, 221);
            cursor: pointer;
            font-weight: bold;
            font-size: 1rem;
        }

        .set-default:hover {
            background-color: rgb(232, 221, 221);
            color: black;
            transition: .3s;
        }

        dialog {
            position: fixed;
            top: 30%;
            left: 50%;
            transform: translate(-50%, -50%);
            gap: 1rem;
            padding: 1rem;
            background: transparent;
            border-radius: .5rem;
            border: 2px solid rgb(232, 221, 221);

            input {
                width: 400px;
                font-size: 1.1rem;
                border: none;
                border-bottom: 2px solid rgb(232, 221, 221);
                background-color: transparent;
                padding: .2rem;
                color: rgb(232, 221, 221);;
                margin-bottom: 1rem;
            }
            input::placeholder {
                color: rgb(232, 221, 221);
            }

            input:focus {
                outline: none;
            }

            .btn-container {
                display: flex;
                justify-content: space-between;
                gap: 1rem;
                
                button {
                    width: 100%;
                    background: transparent;
                    border: none;
                    cursor: pointer;
                    border: 1px solid rgb(232, 221, 221);;
                    padding: .5rem 1rem;
                    font-size: 1rem;
                    font-weight: bold;
                    color: white

                }

                button:hover {
                    background-color: rgb(232, 221, 221);
                    color: black;
                    transition: .3s;
                }
            }
        }
    }

    .top-right {
        display: flex;
        flex-direction: column;
        gap: 1.5rem;

        li {
            display: flex;
            gap: .8rem;
            align-items: center;

            .top-right-icon {
                width: 40px;
                height: 40px;
            }

            .top-right-container {
                
                h4 {
                    font-weight: 500;
                    font-size: 1rem;
                }

                p {
                    font-weight: bold;
                    font-size: 1.5rem;
                }
            }
        }
    }

    /* forecasts */
    .future-forecasts {
        display: flex;
        flex-direction: column;
        gap: 1rem;
        margin-top: 5rem;
        align-items: flex-start;

        .forecasts {
            display: flex;
            gap: 5.5rem;

            li {
                display: flex;
                flex-direction: column;
                gap: .3rem;
                
                h3 {
                    font-weight: 500;
                }

                p {
                    font-size: 1.1rem;
                }
            }

            img {
                width: 80px;
                height: 80px;
            }
        }
    }


}

body::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3); 
    z-index: -1;
}
